import React from 'react';
import Select from 'react-select';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import '../../styles/modal.scss';
import UserController from '../../controllers/userController';
import ProjectController from '../../controllers/projectController';

export default class InviteToProjectModal extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      projectOptions: [],
      selectedProject: null
    }

    this.onChange = this.onChange.bind(this);
  }

  async componentDidMount() {
    if (!UserController.isLoggedIn()) {
      return;
    }
    
    const profileId = UserController.getToken().sub;
    const projects = await ProjectController.getOwnedProjects(profileId);
    this.setState({
      projectOptions: projects.map(this.convertToOption)
    });
  }

  convertToOption(project) {
    return { label: project.name, value: project.id };
  }

  onChange(selectedProject) {
    this.setState({ selectedProject });
  }

  render() {
    const { show, onClose, onConfirm } = this.props;
    const { projectOptions, selectedProject } = this.state;
    const isConfirmDisabled = selectedProject === null;

    return (
      <Modal show={show} animation={false} onHide={onClose}>
        <Modal.Header closeButton>
          <Modal.Title>Invite to Project</Modal.Title>
        </Modal.Header>

        <Modal.Body>
          Send an invitation for the following project:
          <Select className="modal-select"
            variant="dark"
            options={projectOptions}
            value={selectedProject}
            onChange={this.onChange}
          />
        </Modal.Body>

        <Modal.Footer>
          <Button variant="dark" onClick={onClose}>Close</Button>
          <Button
            variant="dark" 
            disabled={isConfirmDisabled}
            onClick={() => onConfirm(selectedProject.value)}
          >
            Confirm
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}