import React from 'react';
import { Helmet } from 'react-helmet';
import { withRouter } from 'react-router-dom';
import { withContext } from '../components/layout';
import Card from 'react-bootstrap/Card';
import ProfileController from '../controllers/profileController';
import ProfileHeader from '../components/profile/profileHeader';
import ProfileOverview from '../components/profile/profileOverview';
import ProfileProjects from '../components/profile/profileProjects';
import ProfileInbox from '../components/profile/profileInbox';
import ProfileNux from '../components/profile/profileNux';
import Loading from '../components/loading';
import InviteToProjectModal from '../components/modals/inviteToProjectModal';
import ProfilePictureModal from '../components/modals/profilePictureModal';
import ProjectController from '../controllers/projectController';
import UserController from '../controllers/userController';
import WebController from '../controllers/webController';
import NuxController from '../controllers/nuxController';
import '../styles/profile.scss';

export const ProfileTab = {
  OVERVIEW: 'overview',
  PROJECTS: 'projects',
  INBOX: 'inbox'
};

class Profile extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      profile: null,
      editableProfile: null,
      isEditable: false,
      tab: ProfileTab.OVERVIEW,
      finishedLoading: false,
      showInviteModal: false,
      showPictureModal: false,
      runNux: false
    };

    this.toggleEditable = this.toggleEditable.bind(this);
    this.onProfileFieldEdit = this.onProfileFieldEdit.bind(this);
    this.saveProfile = this.saveProfile.bind(this);
    this.updatePicture = this.updatePicture.bind(this);
    this.inviteToProject = this.inviteToProject.bind(this);
    this.changeTab = this.changeTab.bind(this);
    this.triggerNux = this.triggerNux.bind(this);
  }

  async componentDidMount() {    
    const { match: { params } } = this.props;
    const username = params.username;

    if (!username) {
      this.setState({ finishedLoading: true });
      return;
    }

    try {
      const profile = await ProfileController.getProfile(username);
      this.setState({
        profile,
        editableProfile: { ...profile },
        finishedLoading: true
      });
      this.triggerNux();
    } catch (error) {
      this.setState({ finishedLoading: true });
      console.log(error);
    }
  }

  componentDidUpdate() {
    this.setTabFromURL();
  }

  setTabFromURL() {
    const { tab } = this.state;
    const params = WebController.getQueryParams();
    const nextTab = params.get('tab') || ProfileTab.OVERVIEW;

    if (nextTab !== tab) {
      this.changeTab(nextTab);
    }
  }

  changeTab(tab) {
    this.setState({ tab });

    const params = WebController.getQueryParams();
    params.set('tab', tab);
    
    WebController.updateQueryParams(params);
  }

  triggerNux() {
    const { profile, runNux} = this.state;
    if (!profile || runNux) {
      return;
    }

    const isOwnProfile = UserController.isLoggedIn()
      && UserController.getToken().username === profile.username;

    if (isOwnProfile && !NuxController.hasShownProfileNux()) {
      this.changeTab(ProfileTab.OVERVIEW);
      NuxController.setProfileNuxShown();
      this.setState({ runNux: true });
    }
  }

  toggleEditable() {
    this.setState(prevState => ({
      isEditable: !prevState.isEditable
    }));
  }

  onProfileFieldEdit(field, value) {
    const { editableProfile } = this.state;
    editableProfile[field] = value;
    this.setState({ editableProfile });
  }

  async saveProfile() {
    const { editableProfile } = this.state;
    try {
      await ProfileController.updateProfile(editableProfile);
      this.setState({
        profile: { ...editableProfile }
      });
      this.toggleEditable();
    } catch (error) {
      console.log(error);
    }
  }

  async updatePicture(pictureUrl) {
    this.setState({ showPictureModal: false });

    if (pictureUrl) {
      const { editableProfile } = this.state;
      editableProfile.pictureUrl = pictureUrl;
      await this.saveProfile();
    }
  }

  async inviteToProject(projectId) {
    const { profile } = this.state;
    const { showAlert } = this.props;
    this.setState({ showInviteModal: false });

    try {
      await ProjectController.createInvite(profile.id, projectId);
      showAlert(
        'You have invited this user to your project.',
        'success'
      );
    } catch (error) {
      showAlert('You have already invited this user.', 'warning');
    }
  }

  render() {
    const {
      profile,
      editableProfile,
      tab,
      isEditable,
      finishedLoading,
      showInviteModal,
      showPictureModal,
      runNux
    } = this.state;

    if (!profile) {
      return (
        <>
          <Helmet>
            <title>Profile</title>
          </Helmet>
          {finishedLoading
            ? <h5>Profile not found.</h5>
            : <Loading />
          }
        </>
      );
    }

    return (
      <>
        <Helmet>
          <title>{profile.username}</title>
        </Helmet>
        {runNux && <ProfileNux />}
        <InviteToProjectModal
          show={showInviteModal}
          onConfirm={this.inviteToProject}
          onClose={() => this.setState({ showInviteModal: false })}
        />
        <ProfilePictureModal
          show={showPictureModal}
          onConfirm={this.updatePicture}
          onClose={() => this.setState({ showPictureModal: false })}
        />
        <Card className="profile-container" bg="secondary" border="secondary">
          <ProfileHeader
            profile={profile}
            tab={tab}
            isEditable={isEditable}
            onSaveProfile={this.saveProfile}
            onToggleEditable={this.toggleEditable}
            onInvite={() => this.setState({ showInviteModal: true })}
            onUpdatePicture={() => this.setState({ showPictureModal: true })}
            onTabChange={this.changeTab}
          />
          <Card.Body>
            {tab === ProfileTab.OVERVIEW &&
              <ProfileOverview
                profile={profile}
                editableProfile={editableProfile}
                isEditable={isEditable}
                onProfileFieldEdit={this.onProfileFieldEdit}
              />
            }
            {tab === ProfileTab.PROJECTS &&
              <ProfileProjects profile={profile} />
            }
            {tab === ProfileTab.INBOX &&
              <ProfileInbox profile={profile} />
            }
          </Card.Body>
        </Card>
      </>
    );
  }
}

export default withRouter(withContext(Profile, 'profile-page'));