import React from "react";
import { Form, } from 'react-bootstrap';
import TagSelect from '../TagSelect';

export default class EditableProfile extends React.Component {
  constructor(props) {
    super(props);
    
    this.onChange = this.onChange.bind(this);
    this.convertToOptions = this.convertToOptions.bind(this);
  }

  onChange(field, value) {
    this.props.onProfileFieldEdit(field, value);
  }

  convertToOptions(items) {
    return items.map(item => ({
      label: item,
      value: item
    }));
  }

  render() {
    const { profile } = this.props;

    return (
      <Form>
        <h4><span>👩‍🔬</span> Bio</h4>
        <Form.Control
          as="textarea"
          rows="3"
          value={profile.bio || ''}
          onChange={event => this.onChange('bio', event.target.value)}
        />
        <hr />
        <h4><span>🧪</span> Interests</h4>
        <TagSelect
          type="interests"
          value={profile.interests}
          onChange={interests => this.onChange('interests', interests)}
        />
        <hr />
        <h4><span>🧰</span> Skills</h4>
        <TagSelect
          type="skills"
          value={profile.languages}
          onChange={languages => this.onChange('languages', languages)}
        />
      </Form>
    );
  }
}