import gql from 'graphql-tag';
import Cookies from 'universal-cookie';
import jwtDecode from 'jwt-decode';
import WebController from '../controllers/webController';
import { client } from './graphqlClient';

const API_PREFIX = process.env.NODE_ENV === 'development'
  ? 'http://localhost:3000/'
  : 'https://quaranteamtech.herokuapp.com/';
const GITHUB_AUTH_ENDPOINT = 'auth/github';

const cookies = new Cookies();

const REGISTER_MUTATION = gql`
  mutation ($username: String!, $email: String!, $password:String!) {
    register(
      input: {
        username: $username
        email: $email
        password: $password
      }
    ) { jwtToken }
  }`;

const LOGIN_MUTATION = gql`
  mutation ($email: String!, $password:String!) {
    login(
      input: {
        email: $email
        password: $password
      }
    ) { jwtToken }
  }`;

async function register(email, username, password) {
  try {
    const res = await client.mutate({
      mutation: REGISTER_MUTATION,
      variables: { email, username, password }
    });

    const jwt = res.data.register.jwtToken;
    cookies.set('jwt', jwt, { path: '/' });

    WebController.navigate(`/#/profile/${username}`);
  } catch (error) {
    throw {
      message: 'An account already exists with this email or username.'
    };
  }
}

async function login(email, password) {
  try {
    const res = await client.mutate({
      mutation: LOGIN_MUTATION,
      variables: { email, password }
    });

    const jwt = res.data.login.jwtToken;
    if (jwt) {
      cookies.set('jwt', jwt, { path: '/' });
      return { jwt };
    } else {
      throw { message: 'Invalid username or password.' };
    }
  } catch (error) {
    throw error;
  }
}

function handleGitHubLogin() {
  const params = WebController.getQueryParams();
  const jwt = params.get('jwt');
  params.delete('jwt');
  WebController.updateQueryParams(params);

  if (jwt) {
    cookies.set('jwt', jwt, { path: '/' });
    WebController.navigate(`/#/profile/${getToken().username}`);
  }
}

function isLoggedIn() {
  const jwt = cookies.get('jwt');
  if (!jwt) {
    return false;
  }
  try {
    jwtDecode(jwt);
    return true;
  } catch {
    return false;
  }
}

function getToken() {
  const jwt = cookies.get('jwt');
  if (!jwt) {
    return null;
  }
  try {
    return jwtDecode(jwt);
  } catch {
    return null;
  }
}

function logOut() {
  cookies.remove('jwt', { path: '/' });
}

export default {
  register,
  login,
  handleGitHubLogin,
  isLoggedIn,
  logOut,
  getToken,
  GITHUB_AUTH_URL: API_PREFIX + GITHUB_AUTH_ENDPOINT
};

export const LoginIntent = {
  PROFILE: 'profile',
  CREATE_PROJECT: 'create_project'
};