import React from "react";
import { Button, Col } from 'react-bootstrap';
import Alert from 'react-bootstrap/Alert'
import Form from 'react-bootstrap/Form';
import { FaGithub } from 'react-icons/fa';
import Modal from 'react-bootstrap/Modal';
import UserController from '../../controllers/userController';
import KeyboardEventHandler from 'react-keyboard-event-handler';
import '../../styles/auth.scss';

export default class LoginModal extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      email: '',
      password: '',
      isLoading: false,
      errorMessage: null
    };

    this.onChange = this.onChange.bind(this);
    this.login = this.login.bind(this);
  }

  onChange(field, event) {
    const value = event.target.value;
    this.setState({ [field]: value });
  }

  async login() {
    this.setState({ isLoading: true });
    const { onLogin } = this.props;
    const { email, password } = this.state;

    try {
      await UserController.login(email, password);
      onLogin();
    } catch (error) {
      this.setState({ errorMessage: error.message });
    }
    this.setState({ isLoading: false });
  }

  render() {
    const { show, onClose } = this.props;
    const { errorMessage, isLoading } = this.state;

    return (
      <Modal
        show={show}
        className="login"
        onHide={onClose}
        size="md"
        centered
      >
        <KeyboardEventHandler
          handleFocusableElements={true}
          handleKeys={['enter']}
          onKeyEvent={this.login}
        />
        <Col>
          <Modal.Header closeButton>
            <Modal.Title>
              <span>👩‍🔬</span> Login
            </Modal.Title>
          </Modal.Header>
          <Modal.Body className="show-grid">
            {errorMessage && <Alert variant="danger">{errorMessage}</Alert>}
            <Form>
              <Form.Group>
                <Form.Control type="email" placeholder="Email" onChange={(event) => this.onChange('email', event)} />
              </Form.Group>
              <Form.Group>
                <Form.Control type="password" placeholder="Password" onChange={(event) => this.onChange('password', event)} />
              </Form.Group>
            </Form>
          </Modal.Body>
          <Modal.Footer>
            <Button
              variant="primary"
              onClick={this.login}
              disabled={isLoading}
              block
            >
              {isLoading ? 'Logging in...' : 'Login'}
            </Button>
            <h5>Or...</h5>
            <Button href={UserController.GITHUB_AUTH_URL} variant="success" block>
              <span><FaGithub className="icon" size="1.8em" /> Login with GitHub</span>
            </Button>
          </Modal.Footer>
          <hr />
          <h6>
            Don't have an account?
            &nbsp;
            <a href="/" style={{ color: '#333333' }}>Register here!</a>
          </h6>
        </Col>
        <br />
      </Modal>
    );
  }
}