import React from 'react';
import ReactDOM from 'react-dom';
import {
  HashRouter,
  Switch,
  Route
} from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';

import Home from './pages/home';
import Projects from './pages/projects';
import Project from './pages/project';
import People from './pages/people';
import Profile from './pages/profile';
import CreateProject from './pages/projects/create';

import createHistory from 'history/createBrowserHistory';
import ReactGA from 'react-ga';

function logAnalytics(location, action) {
  ReactGA.pageview(location.pathname + location.hash);
};

ReactGA.initialize('UA-80633604-4');
logAnalytics(window.location);

const history = createHistory();
history.listen(logAnalytics);

function App() {
  return (
    <HashRouter history={history}>
      <Switch>
        <Route path='/profile/:username'><Profile /></Route>
        <Route path='/projects/create'><CreateProject /></Route>
        <Route path='/projects'><Projects /></Route>
        <Route path='/people'><People /></Route>
        <Route path='/project/:id'><Project /></Route>
        <Route exact path='/'><Home /></Route>
      </Switch>
    </HashRouter>
  );
}

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);