import React from 'react';
import Figure from 'react-bootstrap/Figure';
import Nav from 'react-bootstrap/Nav';
import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import { FaUserEdit, FaCheck, FaTimes, FaCamera } from 'react-icons/fa';
import UserController from '../../controllers/userController';
import { ProfileTab } from '../../pages/profile';
import DEFAULT_PROFILE from '../../images/default-profile.png';
import '../../styles/profile.scss';

export default class ProfileHeader extends React.Component {
  constructor(props) {
    super(props);
  }

  renderNav(profile) {
    const { onTabChange, tab } = this.props;

    const isOwnProfile = UserController.isLoggedIn()
      && UserController.getToken().username === profile.username;

    return (
      <>
        <Nav fill variant="pills" activeKey={tab}>
          <Nav.Item className="profile-overview-nav" onClick={() => onTabChange(ProfileTab.OVERVIEW)}>
            <Nav.Link eventKey="overview">Overview</Nav.Link>
          </Nav.Item>
          <Nav.Item className="profile-projects-nav" onClick={() => onTabChange(ProfileTab.PROJECTS)}>
            <Nav.Link eventKey="projects">Projects</Nav.Link>
          </Nav.Item>
          {isOwnProfile && (
            <Nav.Item className="profile-inbox-nav" onClick={() => onTabChange(ProfileTab.INBOX)}>
              <Nav.Link eventKey="inbox">Inbox</Nav.Link>
            </Nav.Item>
          )}
        </Nav>
        <hr />
      </>
    );
  }

  renderPicture(profile) {
    const { isEditable, onUpdatePicture } = this.props;

    return (
      <Col lg={{ span: 3, offset: 3 }} xs={{ span: 6, offset: 3 }}>
        <Figure>
          <Figure.Image
            src={profile.pictureUrl || DEFAULT_PROFILE}
            roundedCircle
          />
          {isEditable &&
            <Figure.Caption onClick={onUpdatePicture}>
              <FaCamera /> Update picture
            </Figure.Caption>
          }
        </Figure>
      </Col>
    );
  }

  renderHeaderRight(profile) {
    const {
      isEditable,
      onToggleEditable,
      onInvite,
      onSaveProfile,
      tab
    } = this.props;

    const isOwnProfile = UserController.isLoggedIn()
      && UserController.getToken().username === profile.username;
    const showEditOptions = tab === ProfileTab.OVERVIEW && isOwnProfile;

    return (
      <>
        <h2 className="username-title"><b>
          {profile.username}
          {showEditOptions && isEditable && (
            <>
              <FaCheck title="Save Changes" onClick={onSaveProfile} />
              <FaTimes title="Revert Changes" onClick={onToggleEditable} />
            </>
          )}
          {showEditOptions && !isEditable && (
            <FaUserEdit className="edit-toggle" title="Edit Profile" onClick={onToggleEditable} />
          )}
        </b></h2>
        {UserController.isLoggedIn() && !isOwnProfile &&
          <Button size="sm" variant="dark" onClick={onInvite}>
            Invite to Project
          </Button>
        }
      </>
    );
  }

  render() {
    const { profile } = this.props;

    return (
      <Card.Header className="profile-header">
        {this.renderNav(profile)}
        <Row>
          {this.renderPicture(profile)}
          <Col className="profile-header-right" xs={{ span: false }}>
            {this.renderHeaderRight(profile)}
          </Col>
        </Row>
      </Card.Header>
    );
  }
}