import React from "react";
import { Form } from 'react-bootstrap';
import TagSelect from "../TagSelect";
import Select from 'react-select';

const NAME_MAX_LENGTH = 80;

const STATUS_OPTIONS =
  ['Planning Phase', 'Design Phase', 'Development Phase', 'Testing Phase', 'Deployment Phase']
    .map(status => ({ value: status, label: status }));

export default class EditableProject extends React.Component {
  constructor(props) {
    super(props);

    this.onChange = this.onChange.bind(this);
    this.convertToOption = this.convertToOption.bind(this);
  }

  onChange(field, value) {
    this.props.onProjectFieldEdit(field, value);
  }

  convertToOption(item) {
    return {
      label: item,
      value: item
    };
  }

  render() {
    const { project } = this.props;

    return (
      <Form>
        <h4><span>📋</span> Name <small>(required)</small></h4>
        <Form.Control
          value={project.name}
          placeholder="A short title"
          onChange={event => this.onChange('name', event.target.value)}
          maxLength={NAME_MAX_LENGTH}
        />
        <hr />
        <h4><span>ℹ️</span> Description</h4>
        <Form.Control
          as="textarea"
          rows="5"
          value={project.description || ''}
          placeholder={'The project description is shown publicly to'
                      + ' users who are interested in your project'
                      + ' (Markdown is supported).'}
          onChange={event => this.onChange('description', event.target.value)}
        />
        <hr />
        <h4><span>💻</span> Directions</h4>
        <Form.Control
          as="textarea"
          rows="5"
          value={project.directions || ''}
          placeholder={'Directions are shown only to project members,'
                      + ' and should include information like Discord/Slack'
                      + ' links, onboarding directions, etc (Markdown is supported).'}
          onChange={event => this.onChange('directions', event.target.value)}
        />
        <hr />
        <h4><span>🧰</span> Skills</h4>
        <TagSelect
          type="skills"
          value={project.languages}
          onChange={languages => this.onChange('languages', languages)}
        />
        <hr />
        <h4><span>🧪</span> Interest Areas</h4>
        <TagSelect
          type="interests"
          value={project.interests}
          onChange={interests => this.onChange('interests', interests)}
        />
        <hr />
        <h4><span>👩‍🔬</span> Joinable By Others?</h4>
        <Form.Control
          as="select"
          onChange={event => this.onChange('selfJoinable', event.target.value === 'true')}
          value={project.selfJoinable}
        >
          <option value={true}>Yes! Anyone can join on their own.</option>
          <option value={false}>No, people need to ask before joining.</option>
        </Form.Control>
        <hr />
        <h4><span>⏰</span> Project Status</h4>
        <Select
          type="status"
          options={STATUS_OPTIONS}
          value={this.convertToOption(project.status)}
          onChange={status => this.onChange('status', status.value)}
        />
        <hr />
        <h4><span>👀</span> Hidden from others?</h4>
        <Form.Control
          as="select"
          onChange={event => this.onChange('type', event.target.value)}
          value={project.type}
        >
          <option value="public">No! The project will be visible to all!</option>
          <option value="private">Yes, only team members can see the project.</option>
        </Form.Control>
      </Form>
    );
  }
}
