import React from "react";
import '../../styles/profile.scss';
import { Card } from "react-bootstrap";
import Image from 'react-bootstrap/Image';
import Badge from 'react-bootstrap/Badge';
import '../../styles/searchCard.scss';
import DEFAULT_PROFILE from '../../images/default-profile.png';
import { DEFAULT_BIO } from '../profile/readOnlyProfile';

const TAG_DISPLAY_COUNT = 5;

export default function ProfileCard({ profile }) {
  const extraInterests =
    profile.interests.length > TAG_DISPLAY_COUNT;
  const extraSkills =
    profile.languages.length > TAG_DISPLAY_COUNT;

  return (
    <Card className="search-card" bg="secondary">
      <a href={`/#/profile/${profile.username}`} style={{ color: 'inherit', textDecoration: 'inherit' }}>
        <Card.Body>
          <Card.Header className="card-header">
            <span>
              <Image
                src={profile.pictureUrl || DEFAULT_PROFILE}
                roundedCircle
              />
              {profile.username}
            </span>
          </Card.Header>
          <Card.Text>
            {profile.bio || DEFAULT_BIO}
          </Card.Text>
        </Card.Body>

        <Card.Footer>
          {profile.interests.length === 0
            && profile.languages.length === 0 && (
              <>No interests or skills listed</>
            )}

          {profile.interests.length > 0 && (
            <>
              <b>Interests: </b>
              {profile.interests.splice(0, TAG_DISPLAY_COUNT)
                .map(interest => (
                  <Badge pill key={interest} variant="dark">
                    {interest}
                  </Badge>
                ))}
              {extraInterests && <>&nbsp;...</>}
              <br />
            </>
          )}

          {profile.languages.length > 0 && (
            <>
              <b>Skills: </b>
              {profile.languages.splice(0, TAG_DISPLAY_COUNT)
                .map(language => (
                  <Badge pill key={language} variant="dark">
                    {language}
                  </Badge>
                ))}
              {extraSkills && <>&nbsp;...</>}
            </>
          )}
        </Card.Footer>
      </a>
    </Card>
  );
}