import React from 'react';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import Button from 'react-bootstrap/Button';
import { VerticalTimeline, VerticalTimelineElement } from 'react-vertical-timeline-component';
import { FaUser, FaLightbulb, FaPlus, FaUserFriends, FaLaptopCode, FaSearch } from 'react-icons/fa';
import 'react-vertical-timeline-component/style.min.css';
import '../styles/homeTimeline.scss';

const TimelineTab = {
  OWNER: 'owner',
  MEMBER: 'member'
};

const TIMELINE_ELEMENT_PROPS = {
  contentStyle: { background: '#6c757d', color: '#fff' },
  contentArrowStyle: { borderRight: '7px solid #6c757d' },
  iconStyle: { background: '#6c757d', color: '#fff' }
};

const OWNER_STEPS = [
  {
    title: 'Register',
    text: 'Create a profile on Quaranteam, including a bio, your skills, and your interest areas.',
    icon: <FaUser />
  },
  {
    title: 'Ideate',
    text: 'Come up with an exciting idea for a side project you\'d like to work on.',
    icon: <FaLightbulb />
  },
  {
    title: 'Create a Project',
    text: 'Create a project listing on Quaranteam. You will be able to specify the project\'s'
      + ' description, relevant interest areas, and skills required. You can'
      + ' also configure whether members can join directly, or whether they must be invited by you.',
    icon: <FaPlus />
  },
  {
    title: 'Find Members',
    text: 'Users can request to join your project themselves, or you can invite them to your'
      + ' project yourself. There are many talented users on Quaranteam!',
    icon: <FaUserFriends />
  },
  {
    title: 'Develop!',
    text: 'Now that the project has a team, development can begin. As the owner, you can coordinate'
      + ' with teammates as to how to collaborate. Good luck!',
    icon: <FaLaptopCode />
  }
];

const MEMBER_STEPS = [
  {
    title: 'Register',
    text: 'Create a profile on Quaranteam, including a bio, your skills, and your interest areas.',
    icon: <FaUser />
  },
  {
    title: 'Find a Project',
    text: 'Quaranteam hosts numerous exciting projects. Search through the listings and find'
      + ' one that matches your skills and interest areas.',
    icon: <FaSearch />
  },
  {
    title: 'Join a Project',
    text: 'Once you\'ve found an exciting project, the next step is to join. Some projects can be'
      + ' joined immediately; for other, you will need to request to join.',
    icon: <FaUserFriends />
  },
  {
    title: 'Develop!',
    text: 'Now that you\'ve joined a project, you can begin developing. The project owner will provide'
      + ' guidelines on how to collaborate. Good luck!',
    icon: <FaLaptopCode />
  }
];

export default class HomeTimeline extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      tab: TimelineTab.OWNER
    };

    this.changeTab = this.changeTab.bind(this);
  }

  changeTab(tab) {
    this.setState({ tab });
  }

  render() {
    const { tab } = this.state;
    const steps = tab === TimelineTab.OWNER ? OWNER_STEPS : MEMBER_STEPS;

    return (
      <div className="home-timeline">
        <h1 className="timeline-header"><span>🤔</span> How does it work?</h1>
        <ButtonGroup className="tab-changer">
          <Button
            onClick={() => this.changeTab(TimelineTab.OWNER)}
            active={tab === TimelineTab.OWNER}
            variant="secondary"
          >
            I have a project idea
          </Button>
          <Button
            onClick={() => this.changeTab(TimelineTab.MEMBER)}
            active={tab === TimelineTab.MEMBER}
            variant="secondary"
          >
            I want to join a project
          </Button>
        </ButtonGroup>
        <VerticalTimeline>
          {steps.map((step, i) => (
            <VerticalTimelineElement 
              {...TIMELINE_ELEMENT_PROPS}
              icon={step.icon}
              key={i}
            >
              <h3>{step.title}</h3>
              <p className="element-text">
                {step.text}
              </p>
            </VerticalTimelineElement>
          ))}
        </VerticalTimeline>
      </div>
    );
  }
}