import React from "react";
import { withRouter, Link } from 'react-router-dom';
import { Button } from 'react-bootstrap';
import Alert from 'react-bootstrap/Alert'
import Form from 'react-bootstrap/Form';
import { FaGithub } from 'react-icons/fa';
import UserController, { LoginIntent } from '../controllers/userController';
import WebController from '../controllers/webController';
import '../styles/auth.scss';
import { withContext } from './layout';

const ERROR_MESSAGES = {
  'duplicate': 'An account already exists with this email or username.'
};

class Register extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      email: '',
      username: '',
      password: '',
      errorMessage: null
    };

    this.onChange = this.onChange.bind(this);
    this.register = this.register.bind(this);
  }

  componentDidMount() {
    const params = WebController.getQueryParams();
    const error = params.get('error');

    if (error) {
      this.setState({
        errorMessage: ERROR_MESSAGES[error] || 'An unknown error occurred.'
      });
    }
  }

  onChange(field, event) {
    const value = event.target.value;
    this.setState({ [field]: value });
  }

  async register() {
    const { email, username, password } = this.state;

    try {
      await UserController.register(email, username, password);
      WebController.navigate(`/#/profile/${username}`);
    } catch (error) {
      this.setState({ errorMessage: error.message });
    }
  }

  render() {
    const { showLoginModal } = this.props;
    const {
      email,
      username,
      password,
      errorMessage
    } = this.state;

    const isRegisterDisabled = email.trim() === '' ||
      username.trim() === '' || password.trim() === '';

    return (
        <div className="register">
          <h1>
            Create an Account
          </h1>
          {errorMessage && <Alert variant="danger">{errorMessage}</Alert>}
          <Form>
            <Form.Group>
              <Form.Control type="email" placeholder="Email" onChange={(event) => this.onChange('email', event)}/>
            </Form.Group>
            <Form.Group>
              <Form.Control type="text" placeholder="Username" onChange={(event) => this.onChange('username', event)}/>
            </Form.Group>
            <Form.Group>
              <Form.Control type="password" placeholder="Password" onChange={(event) => this.onChange('password', event)}/>
            </Form.Group>
            <Button onClick={this.register} variant="primary" block disabled={isRegisterDisabled}>
              Register
            </Button>
            <h6>Or...</h6>
            <Button href={UserController.GITHUB_AUTH_URL} variant="success" block>
              <span><FaGithub className="icon" size="1.8em"/> Register with GitHub</span>
            </Button>
            <h6>
              Already have an account?
              <b>
                <Link to="#" onClick={() => showLoginModal(LoginIntent.PROFILE)}> Login</Link>
              </b>
            </h6>
          </Form>
        </div>
    );
  }
}

export default withContext(withRouter(Register));