import React from 'react';
import { withRouter } from 'react-router-dom';
import Badge from 'react-bootstrap/Badge';
import Button from 'react-bootstrap/Button';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import ListGroup from 'react-bootstrap/ListGroup';
import RequestController from '../../controllers/requestController';
import UserController from '../../controllers/userController';
import WebController from '../../controllers/webController';
import '../../styles/profile.scss';

const PROJECT_STATUS = {
  'pending': { color: 'dark' },
  'accepted': { color: 'success' },
  'declined': { color: 'danger' }
};

class ProfileInbox extends React.Component {
  constructor(props) {
    super(props);

    this.accept = this.accept.bind(this);
    this.decline = this.decline.bind(this);
  }

  async accept(projectId) {
    const profileId = UserController.getToken().sub;
    try {
      await RequestController.accept(profileId, projectId);
      WebController.navigate(`/#/project/${projectId}`);
    } catch (error) {
      console.log(error);
    }
  }

  async decline(requestId) {
    try {
      await RequestController.decline(requestId);
      window.location.reload();
    } catch (error) {
      console.log(error);
    }
  }

  renderRequests(profile) {
    const requestedProjects = profile.pendingProjects.nodes
      .filter(p => p.requestType === 'request' && p.requestStatus === 'pending');

    return (
      <>
        {requestedProjects.length === 0 &&
          <>
            <span className="message">
              You have not requested to join any projects.
            </span>
            <br />
          </>
        }
        <div className="profile-badges">
          {requestedProjects.map(p => (
            <Badge pill key={p.id} variant={PROJECT_STATUS[p.requestStatus].color}>
              <a href={`/#/project/${p.id}`}>
                {p.name}
                &nbsp;({p.requestStatus})
              </a>
            </Badge>
          ))}
        </div>
      </>
    );
  }

  renderInvites(profile) {
    const invitedProjects = profile.pendingProjects.nodes
      .filter(p => p.requestType === 'invite' && p.requestStatus === 'pending');

    return (
      <>
        {invitedProjects.length === 0 &&
          <span className="message">
            You have not been invited to any projects.
          </span>
        }
        <ListGroup>
          {invitedProjects.map(p => (
            <ListGroup.Item key={p.requestId} className="invite-list-item">
              <a href={`/#/project/${p.id}`}>{p.name}</a>
              <ButtonGroup className="invite-actions" size="sm">
                <Button
                  variant="success"
                  onClick={() => this.accept(p.id)}
                >
                  Accept
                </Button>
                <Button
                  variant="danger"
                  onClick={() => this.decline(p.requestId)}
                >
                  Decline
                </Button>
              </ButtonGroup>
            </ListGroup.Item>
          ))}
        </ListGroup>
      </>
    );
  }

  render() {
    const { profile } = this.props;

    return (
      <div className="profile-inbox">
        <h4>Requests</h4>
        {this.renderRequests(profile)}
        <hr />
        <h4>Invites</h4>
        {this.renderInvites(profile)}
      </div>
    );
  }
}

export default withRouter(ProfileInbox);