import React from 'react';
import '../../styles/profile.scss';
import Button from 'react-bootstrap/Button';
import UserController from '../../controllers/userController';

export default function JoinProjectButton({ project, onClick }) {
  if (!UserController.isLoggedIn()) {
    return null;
  }

  const profileId = UserController.getToken().sub;
  const isOwner = profileId === project.ownerId;
  const isMember = 
    project.members.nodes.some(m => m.id === profileId);

  if (isOwner || isMember) {
    return null;
  }

  const isPendingMember =
    project.pendingMembers.nodes.some(m => m.id === profileId);
  
  if (isPendingMember) {
    return (
      <Button variant="dark" size="sm" disabled>
        Awaiting request response
      </Button>
    );
  }

  return (
    <Button variant="dark" size="sm" onClick={onClick}>
      {project.selfJoinable ? 'Join Project' : 'Request to Join'}
    </Button>
  );
}