import React, { Component } from "react";
import { withRouter } from 'react-router-dom';
import { withContext } from '../components/layout';
import { Card } from 'react-bootstrap';
import ProjectHeader from "../components/project/projectHeader";
import ProjectController from "../controllers/projectController";
import RequestController from "../controllers/requestController";
import UserController from '../controllers/userController';
import WebController from '../controllers/webController';
import Loading from '../components/loading';
import InviteMemberModal from '../components/modals/inviteMemberModal';
import ProjectOverview from '../components/project/projectOverview';
import ProjectOwnerNux from '../components/project/projectOwnerNux';
import ProjectMemberNux from '../components/project/projectMemberNux';
import NuxController from '../controllers/nuxController';
import ManageProjectMembers from '../components/project/manageProjectMembers';
import '../styles/project.scss';

export const ProjectTab = {
  OVERVIEW: 'overview',
  MANAGE: 'manage'
};

class Project extends Component {
  constructor(props) {
    super(props);

    this.state = {
      tab: ProjectTab.OVERVIEW,
      finishedLoading: false,
      project: null,
      editableProject: null,
      isEditable: false,
      showInviteModal: false,
      runOwnerNux: false,
      runMemberNux: false
    };

    this.joinProject = this.joinProject.bind(this);
    this.leaveProject = this.leaveProject.bind(this);
    this.toggleEditable = this.toggleEditable.bind(this);
    this.onProjectFieldEdit = this.onProjectFieldEdit.bind(this);
    this.saveProject = this.saveProject.bind(this);
    this.deleteProject = this.deleteProject.bind(this);
    this.inviteMember = this.inviteMember.bind(this);
    this.changeTab = this.changeTab.bind(this);
  }

  async componentDidMount() {
    const { match: { params } } = this.props;
    const id = Number(params.id);
    
    if (!id) {
      this.setState({ finishedLoading: true });
      return;
    }

    try {
      const project = await ProjectController.getProject(id);
      this.setState({
        project,
        editableProject: { ...project },
        finishedLoading: true
      });
      this.triggerNux();
      this.setTabFromURL();
    } catch (error) {
      this.setState({ finishedLoading: true });
      console.log(error);
    }
  }

  triggerNux() {
    const { project, runOwnerNux, runMemberNux } = this.state;

    if (!UserController.isLoggedIn() || !project) {
      return;
    }
    if (runOwnerNux || runMemberNux) {
      return;
    }

    const isProjectOwner = project.ownerId === UserController.getToken().sub;
    const isProjectMember = project.members.nodes.map(m => m.username)
      .includes(UserController.getToken().username);

    if (!NuxController.hasShownOwnerProjectNux() && isProjectOwner) {
      this.changeTab(ProjectTab.OVERVIEW);
      NuxController.setOwnerProjectNuxShown();
      this.setState({ runOwnerNux: true });
    } else if (!NuxController.hasShownMemberProjectNux() && isProjectMember) {
      this.changeTab(ProjectTab.OVERVIEW);
      NuxController.setMemberProjectNuxShown();
      this.setState({ runMemberNux: true });
    }
  }

  componentDidUpdate() {
    this.setTabFromURL();
  }
  
  setTabFromURL() {
    const { project, tab } = this.state;
    const isOwnProject = UserController.isLoggedIn()
      && UserController.getToken().sub === project.ownerId;

    if (!isOwnProject && tab !== ProjectTab.OVERVIEW) {
      this.changeTab(ProjectTab.OVERVIEW);
    }

    const params = WebController.getQueryParams();
    const nextTab = params.get('tab') || ProjectTab.OVERVIEW;
    
    if (nextTab !== tab) {
      this.changeTab(nextTab);
    }
  }

  changeTab(tab) {
    this.setState({ tab });

    const params = WebController.getQueryParams();
    params.set('tab', tab);
    
    WebController.updateQueryParams(params);
  }

  toggleEditable() {
    this.setState(prevState => ({
      isEditable: !prevState.isEditable
    }));
  }

  onProjectFieldEdit(field, value) {
    const { editableProject } = this.state;
    editableProject[field] = value;
    this.setState({ editableProject });
  }

  async saveProject() {
    const { editableProject } = this.state;
    try {
      await ProjectController.updateProject(editableProject);
      this.setState({
        project: { ...editableProject }
      });
      this.toggleEditable();
    } catch (error) {
      console.log(error);
    }
  }

  async deleteProject() {
    const { project } = this.state;
    try {
      await ProjectController.deleteProject(project.id);
      WebController.navigate('/#/projects');
    } catch (error) {
      console.log(error);
    }
  }

  async inviteMember(profileId) {
    const { project } = this.state;
    const { showAlert } = this.props;
    this.setState({ showInviteModal: false });

    try {
      await RequestController.inviteToProject(profileId, project.id);
      showAlert(
        'You have invited the user to your project.',
        'success'
      );
    } catch (error) {
      showAlert('You have already invited this user.', 'warning');
    }
  }

  async joinProject() {
    const { project } = this.state;
    const profileId = UserController.getToken().sub;
    try {
      if (project.selfJoinable) {
        await RequestController.joinProject(
          profileId,
          project.id,
        );
      } else {
        await RequestController.requestToJoinProject(
          profileId,
          project.id,
        );
      }
      window.location.reload();
    } catch (error) {
      console.log(error);
    }
  }

  async leaveProject() {
    const { project } = this.state;
    const profileId = UserController.getToken().sub;

    try {
      await RequestController.leaveProject(
        profileId,
        project.id
      );
      WebController.navigate(`/#/projects`);
    } catch (error) {
      console.log(error);
    }
  }

  render() {
    const {
      tab,
      project,
      finishedLoading,
      isEditable,
      editableProject,
      showInviteModal,
      runOwnerNux,
      runMemberNux
    } = this.state;

    if (!project) {
      return (
        <>
          {finishedLoading
            ? <h5>Project not found.</h5>
            : <Loading />
          }
        </>
      );
    }

    const isEditValid = editableProject && editableProject.name.trim() !== '';

    return (
      <>
        {runOwnerNux && <ProjectOwnerNux />}
        {runMemberNux && <ProjectMemberNux />}
        <InviteMemberModal
          show={showInviteModal}
          onConfirm={this.inviteMember}
          onClose={() => this.setState({ showInviteModal: false })}
        />
        <Card className="project-container" bg="secondary" border="secondary">
          <ProjectHeader
            project={project}
            isEditable={isEditable}
            isEditValid={isEditValid}
            tab={tab}
            onInvite={() => this.setState({ showInviteModal: true })}
            onJoin={this.joinProject}
            onLeave={this.leaveProject}
            onSave={this.saveProject}
            onDelete={this.deleteProject}
            onToggleEditable={this.toggleEditable}
            onTabChange={this.changeTab}
          />

          <Card.Body>
            {tab === ProjectTab.OVERVIEW &&
            <ProjectOverview
              project={project}
              editableProject={editableProject}
              isEditable={isEditable}
              onProjectFieldEdit={this.onProjectFieldEdit}
            />
            }
            {tab === ProjectTab.MANAGE && 
              <ManageProjectMembers project={project} />
            }
          </Card.Body>
        </Card>
      </>
    );
  }
}

export default withContext(withRouter(Project), 'project-page');