import gql from 'graphql-tag';
import { client } from './graphqlClient';

const GET_PROF_COUNT_QUERY = gql`
  query {
    allProfiles {
      totalCount
    }
  }
`;

const GET_PROFILES_QUERY = gql`
  query {
    allProfiles {
      edges {
        node {
          username
          id
          bio
          interests
          languages
          pictureUrl
        }
      }
    }
  }
`;

const GET_FILTERED_PROFILES_QUERY = gql`
  query ($search: String!) {
    allProfiles(filter: {
      or: [
        { username: { includesInsensitive: $search } }
        { bio: { includesInsensitive: $search } }
        { interestsText: { includesInsensitive: $search } }
        { languagesText: { includesInsensitive: $search } }
      ]
    }) {
      edges {
        node {
          username
          id
          bio
          interests
          languages
          pictureUrl
        }
      }
    }
  }
`;

const PROFILE_QUERY = gql`
  query ($username: String!) {
    profileByUsername(username: $username) {
      id, username pictureUrl, 
      bio, interests, languages, tz,
      ownedProjects { nodes { id, name } }
      projects { nodes { id, name } }
      pendingProjects {
        nodes { id, name }
        edges { id, status, type }
      }
    }
  }`;

const UPDATE_PROFILE_MUTATION = gql`
  mutation (
    $username: String!
    $bio: String
    $pictureUrl: String
    $interests: [String]!
    $languages: [String]!
  ) {
    updateProfileByUsername(
      input: {
        username: $username
        profilePatch: {
          bio: $bio
          pictureUrl: $pictureUrl
          interests: $interests
          languages: $languages
        }
      }
    ) { clientMutationId }
  }`;

async function getProfCount() {
  try {
    const res = await client.query({
      query: GET_PROF_COUNT_QUERY
    });

    return res.data.allProfiles.totalCount;
  } catch (error) {
    throw error;
  }
}

async function getProfile(username) {
  try {
    const res = await client.query({
      query: PROFILE_QUERY,
      variables: { username }
    });

    const profile = res.data.profileByUsername;

    // Including request type/status in each pending project
    profile.pendingProjects.nodes = 
      profile.pendingProjects.nodes.map((p, i) => ({
      ...p,
      requestId: profile.pendingProjects.edges[i].id,
      requestStatus: profile.pendingProjects.edges[i].status,
      requestType: profile.pendingProjects.edges[i].type
    }));
    
    return profile;
  } catch (error) {
    throw error;
  }
}

async function updateProfile(profile) {
  try {
    await client.mutate({
      mutation: UPDATE_PROFILE_MUTATION,
      variables: profile
    });
  } catch (error) {
    throw error;
  }
}

async function getProfiles() {
  try {
    const res = await client.query({
      query: GET_PROFILES_QUERY
    });

    return res.data.allProfiles.edges.map(p => p.node);
  } catch (error) {
    throw error;
  }
}

async function getFilteredProfiles(search) {
  try {
    const res = await client.query({
      query: GET_FILTERED_PROFILES_QUERY,
      variables: { search }
    });

    return res.data.allProfiles.edges.map(p => p.node);
  } catch (error) {
    throw error;
  }
}

export default {
  getProfile,
  updateProfile,
  getProfiles,
  getFilteredProfiles,
  getProfCount
};