import React from 'react';
import Select from 'react-select';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import '../../styles/modal.scss';
import UserController from '../../controllers/userController';
import ProfileController from '../../controllers/profileController';

export default class InviteToProjectModal extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      profileOptions: [],
      selectedProfile: null
    }

    this.onChange = this.onChange.bind(this);
  }

  async componentDidMount() {
    if (!UserController.isLoggedIn()) {
      return;
    }

    const profiles = await ProfileController.getProfiles();
    const username = UserController.getToken().username;

    this.setState({
      profileOptions: profiles
        .filter(p => p.username !== username)
        .map(this.convertToOption)
    });
  }

  convertToOption(profile) {
    return { label: profile.username, value: profile.id };
  }

  onChange(selectedProfile) {
    this.setState({ selectedProfile });
  }

  render() {
    const { show, onClose, onConfirm } = this.props;
    const { profileOptions, selectedProfile } = this.state;
    const isConfirmDisabled = selectedProfile === null;

    return (
      <Modal show={show} animation={false} onHide={onClose}>
        <Modal.Header closeButton>
          <Modal.Title>Invite Member</Modal.Title>
        </Modal.Header>

        <Modal.Body>
          Send an invitation to the following user:
          <Select className="modal-select"
            variant="dark"
            options={profileOptions}
            value={selectedProfile}
            onChange={this.onChange}
          />
        </Modal.Body>

        <Modal.Footer>
          <Button variant="dark" onClick={onClose}>Close</Button>
          <Button
            variant="dark" 
            disabled={isConfirmDisabled}
            onClick={() => onConfirm(selectedProfile.value)}
          >
            Confirm
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}