import axios from 'axios';

const IMGUR_CLIENT_ID = '028daff1533a073';
const UPLOAD_ENDPOINT = 'https://api.imgur.com/3/image';

async function upload(imageBlob) {
  const formData = new FormData();
  formData.append('image', imageBlob, 'image');

  const config = {
    headers: {
      'Authorization': `Client-ID ${IMGUR_CLIENT_ID}`
    }
  };

  const res = await axios.post(UPLOAD_ENDPOINT, formData, config);
  return res.data.data.link;
}

export default {
  upload
};