import gql from 'graphql-tag';
import { client } from './graphqlClient';

// Cache of skills and interests
let SKILLS = null;
let INTERESTS = null;

const GET_SKILLS_QUERY = gql`
  query {
    allTags(condition: {type: "skill"}) {
      nodes {
        name
      }
    }
  }
`;

const GET_INTERESTS_QUERY = gql`
  query {
    allTags(condition: {type: "interest"}) {
      nodes {
        name
      }
    }
  }
`;

async function getSkills() {
  if (SKILLS) {
    return SKILLS;
  }

  try {
    const res = await client.query({
      query: GET_SKILLS_QUERY
    });
    SKILLS = res.data.allTags.nodes.map(t => t.name);
    return SKILLS;
  } catch (error) {
    throw error;
  }
}

async function getInterests() {
  if (INTERESTS) {
    return INTERESTS;
  }

  try {
    const res = await client.query({
      query: GET_INTERESTS_QUERY
    });
    INTERESTS = res.data.allTags.nodes.map(t => t.name);
    return INTERESTS;
  } catch (error) {
    throw error;
  }
}

export default {
  getSkills,
  getInterests
};