import React from 'react';
import Card from 'react-bootstrap/Card';
import Badge from 'react-bootstrap/Badge';
import ReactMarkdown from 'react-markdown';
import UserController from '../../controllers/userController';
import '../../styles/markdown.scss';

const DEFAULT_DESCRIPTION = 'This project currently has no description.';
const DEFAULT_DIRECTIONS = 'This project currently has no directions.';

export default function ReadOnlyProject({ project }) {
  const membersCount = project.members.totalCount + 1;

  const isMember = UserController.isLoggedIn()
    && project.members.nodes.some(
      m => m.id === UserController.getToken().sub
    );
  
  const isOwner = UserController.isLoggedIn()
    && project.ownerId === UserController.getToken().sub;

  return (
    <>
      <h4 className="project-description"><span>ℹ️</span> Description</h4>
      <ReactMarkdown
        className="markdown"
        source={project.description || DEFAULT_DESCRIPTION}
      />
      <hr />
      {(isOwner || isMember) &&
        <>
          <h4 className="project-directions"><span>💻</span> Directions</h4>
          <ReactMarkdown
            className="markdown"
            source={project.directions || DEFAULT_DIRECTIONS}
          />
          <hr />
        </>
      }
      <h4 className="project-members"><span>👩‍👩‍👧‍👦</span> Members ({membersCount})</h4>
      <div className="project-members-badges">
        <Badge pill variant="success">
          <a href={`/#/profile/${project.owner.username}`}>
            {project.owner.username} (owner)
            </a>
        </Badge>
        {project.members.nodes.map(member => (
          <Badge pill key={member.username} variant="dark">
            <a href={`/#/profile/${member.username}`}>
              {member.username}
            </a>
          </Badge>
        ))}
      </div>
      <hr />
      <h4 className="project-skills"><span>🧰</span> Skills</h4>
      <div className="project-skills-badges">
        {project.languages.map(lang => (
          <Badge pill key={lang} variant="dark">
            {lang}
          </Badge>
        ))}
      </div>
      <hr />
      <h4 className="project-interests"><span>🧪</span> Interest Areas</h4>
      <div className="project-interests-badges">
        {project.interests.map(interest => (
          <Badge pill key={interest} variant="dark">
            {interest}
          </Badge>
        ))}
      </div>
      <hr />
      <h4 className="project-status"><span>⏰</span> Project Status</h4>
      <Card.Text>
        {project.status}
      </Card.Text>
    </>
  );
}