import React from 'react';
import { Helmet } from 'react-helmet';
import { withRouter } from 'react-router-dom';
import InfiniteScroll from "react-infinite-scroll-component";
import ScrollToTop from 'react-scroll-up';
import ProjectCard from '../components/project/projectCard';
import Loading from '../components/loading';
import { withContext } from '../components/layout';
import { Container, Row, Col, FormControl, Button } from "react-bootstrap";
import ProjectController from '../controllers/projectController';
import UserController, { LoginIntent } from "../controllers/userController";
import { debounce } from 'throttle-debounce';
import { FaArrowUp } from "react-icons/fa";
import '../styles/projects.scss';

const DEBOUNCE_MS = 200;
const PROJECTS_PER_FETCH = 9;
const SCROLL_TO_TOP_SHOW_UNDER = 260;

class Projects extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      projects: [],
      displayedProjects: [],
      search: '',
      finishedLoading: false
    };

    this.retrieveProjects = debounce(
      DEBOUNCE_MS,
      this.retrieveProjects.bind(this)
    );
    this.updateSearch = this.updateSearch.bind(this);
    this.renderHeader = this.renderHeader.bind(this);
    this.renderProjects = this.renderProjects.bind(this);
    this.renderCreateButton = this.renderCreateButton.bind(this);
    this.fetchMoreProjects = this.fetchMoreProjects.bind(this);
  }

  async componentDidMount() {
    this.retrieveProjects();
  }

  async retrieveProjects(search) {
    this.setState({ finishedLoading: false });

    try {
      const projects = search
        ? await ProjectController.getFilteredProjects(search)
        : await ProjectController.getProjects();

      this.setState({
        projects,
        displayedProjects: projects.slice(0, PROJECTS_PER_FETCH)
      });
    } catch (error) {
      console.log(error);
    }

    this.setState({ finishedLoading: true });
  }

  updateSearch(search) {
    this.setState({ search });
  }

  fetchMoreProjects() {
    const { projects, displayedProjects } = this.state;
    const fetchedProjects = projects.slice(
      displayedProjects.length,
      displayedProjects.length + PROJECTS_PER_FETCH
    );

    this.setState({
      displayedProjects: displayedProjects.concat(fetchedProjects)
    });
  }

  renderCreateButton() {
    return (
      <Button
        onClick={() => {
          if (UserController.isLoggedIn()) {
            window.history.pushState({}, '', '/#/projects/create');
            window.location.reload();
          } else {
            const { showLoginModal } = this.props;
            showLoginModal(LoginIntent.CREATE_PROJECT);
          }
        }}
        className="add-project-btn"
        variant="outline-light"
        title="Create a project"
      >
        +
      </Button>
    );
  }

  renderHeader() {
    const { search } = this.state;

    return (
      <>
        <h1>
          <span>💡</span> Projects
            {this.renderCreateButton()}
        </h1>
        <p>
          Discover projects and find one to join!
        </p>
        <FormControl
          placeholder="Search by title, technologies, etc..."
          className="search"
          value={search}
          type="text"
          size="lg"
          onChange={async (event) => {
            const search = event.target.value;
            this.updateSearch(search);
            await this.retrieveProjects(search);
          }}
        />
      </>);
  }

  renderProjects() {
    const { projects, finishedLoading, displayedProjects } = this.state;
    const hasMore = projects.length > displayedProjects.length;

    if (!finishedLoading) {
      return <Loading />
    }

    if (projects.length === 0) {
      return <h4>No projects match the search.</h4>;
    }

    return (
      <InfiniteScroll
        dataLength={displayedProjects.length}
        next={this.fetchMoreProjects}
        hasMore={hasMore}
        loader={<span className="infinite-load-text">Loading...</span>}
      >
        <Row>
          {displayedProjects.map(p => (
            <Col sm="4" key={p.id}>
              <ProjectCard project={p} />
            </Col>
          ))}
        </Row>
      </InfiniteScroll>
    );
  }

  render() {
    return (
      <>
        <Helmet>
          <title>Projects</title>
        </Helmet>
        <Container fluid>
          {this.renderHeader()}
          {this.renderProjects()}
        </Container>
        <ScrollToTop showUnder={SCROLL_TO_TOP_SHOW_UNDER}>
          <h2><FaArrowUp title="Scroll to top" /></h2>
        </ScrollToTop>
      </>
    );
  }
}

export default withContext(withRouter(Projects), 'projects-page');