import React from 'react';
import Select from 'react-select';
import TagController from '../controllers/tagController';

function formTheme(theme) {
  return {
    ...theme,
    colors: {
      ...theme.colors,
      primary25: 'gray',
      primary: '#64485C',
      backgroundColor: 'transparent',
    },
  };
}

export default class TagSelect extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      options: []
    }
  }

  async componentDidMount() {
    const { type } = this.props;

    try {
      const tags = type === 'interests'
        ? await TagController.getInterests()
        : await TagController.getSkills();

      this.setState({
        options: this.convertToOptions(tags)
      });
    } catch (error) {
      console.log(error);
    }
  }

  convertToOptions(items) {
    if (!items) {
      return [];
    }

    return items.map(item => ({
      label: item,
      value: item
    }));
  }

  render() {
    const { options } = this.state;
    const { value, onChange } = this.props;

    return (
      <Select
        theme={formTheme}
        options={options}
        value={this.convertToOptions(value)}
        onChange={items =>
          onChange(items ? items.map(i => i.value) : [])
        }
        isMulti
      />
    );
  }
}