import React from 'react';
import Badge from 'react-bootstrap/Badge';
import UserController from '../../controllers/userController';
import '../../styles/profile.scss';

export default class ProfileProjects extends React.Component {
  constructor(props) {
    super(props);
  }

  getPersonalizedText(isOwnProfile) {
    const { profile } = this.props;
    if (isOwnProfile) {
      return 'You have';
    }
    return `${profile.username} has`;
  }

  renderProjects(projects, actionText, isOwnProfile) {
    return (
      <>
        {projects.length === 0 &&
          <>
            <span className="message">
              {this.getPersonalizedText(isOwnProfile)} not {actionText} any projects.
          </span>
            <br />
          </>
        }
        <div className="profile-badges">
          {projects.map(p => (
            <Badge pill key={p.id} variant="dark">
              <a href={`/#/project/${p.id}`}>{p.name}</a>
            </Badge>
          ))}
        </div>
      </>
    );
  }

  render() {
    const { profile } = this.props;
    const ownedProjects = profile.ownedProjects.nodes;
    const projects = profile.projects.nodes;

    const isOwnProfile = UserController.isLoggedIn()
      && UserController.getToken().username === profile.username;

    return (
      <div className="profile-projects">
        <h4>Owned Projects</h4>
        {this.renderProjects(ownedProjects, 'created', isOwnProfile)}
        <hr />
        <h4>Joined Projects</h4>
        {this.renderProjects(projects, 'joined', isOwnProfile)}
      </div>
    );
  }
}