import React from "react";
import { Card } from "react-bootstrap";
import ReactMarkdown from 'react-markdown';
import Badge from 'react-bootstrap/Badge';
import '../../styles/projects.scss';
import '../../styles/searchCard.scss';
import '../../styles/markdown.scss';

const DEFAULT_DESCRIPTION = 'This project currently has no description.';

export default function ProjectCard({ project }) {
  return (
    <Card className="search-card" bg="secondary">
      <a href={`/#/project/${project.id}`} style={{ color: 'inherit', textDecoration: 'inherit' }}>
        <Card.Body>
          <Card.Header>{project.name}</Card.Header>
          <Card.Text>
            <ReactMarkdown
              className="markdown"
              disallowedTypes={['heading', 'link', 'image']}
              unwrapDisallowed={true}
              source={project.description || DEFAULT_DESCRIPTION}
            />
          </Card.Text>
        </Card.Body>
        <Card.Footer>
          <b>Members:</b> {project.members.totalCount + 1}
          <br />
          <b>Status:</b> {project.status || 'None listed'}
          <br />

          {project.interests.length > 0 && (
            <>
              <b>Interests: </b>
              {project.interests.map(interest => (
                <Badge pill key={interest} variant="dark">
                  {interest}
                </Badge>
              ))}
              <br />
            </>
          )}

          {project.languages.length > 0 && (
            <>
              <b>Skills: </b>
              {project.languages.map(language => (
                <Badge pill key={language} variant="dark">
                  {language}
                </Badge>
              ))}
            </>
          )}
        </Card.Footer>
      </a>
    </Card >
  );
}