import React from 'react';
import { withRouter } from 'react-router-dom';
import Header from './header';
import LoginModal from './modals/loginModal';
import Alert from 'react-bootstrap/Alert';
import '../styles/layout.scss';
import WebController from '../controllers/webController';
import UserController, { LoginIntent } from '../controllers/userController';

export const LayoutContext = React.createContext();

class Layout extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isAlertVisible: false,
      isLoginModalVisible: false,
      loginIntent: null,
      alertType: null,
      alertText: null
    };

    this.followLoginIntent = this.followLoginIntent.bind(this);
    this.showLoginModal = this.showLoginModal.bind(this);
    this.showAlert = this.showAlert.bind(this);
    this.renderAlert = this.renderAlert.bind(this);
  }

  followLoginIntent() {
    this.setState({ isLoginModalVisible: false });
    const { loginIntent } = this.state;

    if (loginIntent === LoginIntent.PROFILE) {
      const username = UserController.getToken().username;
      WebController.navigate(`/#/profile/${username}`)
    } else if (loginIntent === LoginIntent.CREATE_PROJECT) {
      WebController.navigate(`/#/projects/create`);
    }
  }

  showLoginModal(loginIntent) {
    this.setState({
      isLoginModalVisible: true,
      loginIntent
    });
  }

  showAlert(text, type) {
    this.setState({
      isAlertVisible: true,
      alertType: type,
      alertText: text
    });
  }

  renderAlert() {
    const { isAlertVisible, alertType, alertText } = this.state;

    if (!isAlertVisible) {
      return null;
    }

    return (
      <Alert
        className="layout-alert"
        variant={alertType}
        dismissible={true}
        onClose={() => this.setState({ isAlertVisible: false })}
      >
        {alertText}
      </Alert>
    );
  }

  render() {
    const { className, children } = this.props;
    const { isLoginModalVisible } = this.state;

    const context = {
      showAlert: this.showAlert,
      showLoginModal: this.showLoginModal
    };

    return (
      <LayoutContext.Provider value={context}>
        <Header />
        <LoginModal
          show={isLoginModalVisible}
          onClose={() => this.setState({ isLoginModalVisible: false })}
          onLogin={this.followLoginIntent}
        />
        <div className="container-fluid">
          {this.renderAlert()}
          <main className={className}>{children}</main>
        </div>
      </LayoutContext.Provider>
    );
  }
}

export function withContext(Component, pageClassName) {
  return function connectedComponent(props) {
    const consumer = (
      <LayoutContext.Consumer>
        {context =>
          <Component
            {...props}
            showAlert={context.showAlert}
            showLoginModal={context.showLoginModal}
          />
        }
      </LayoutContext.Consumer>
    );

    if (pageClassName) {
      return (
        <Layout className={pageClassName}>
          {consumer}
        </Layout>
      );
    }

    return consumer;
  }
}

export default withRouter(Layout);