import React from 'react';
import EditableProfile from './editableProfile';
import ReadOnlyProfile from './readOnlyProfile';

export default class ProfileOverview extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const {
      profile,
      editableProfile,
      isEditable,
      onProfileFieldEdit
    } = this.props;

    return (
      <>
        {isEditable
          ? <EditableProfile
            onProfileFieldEdit={onProfileFieldEdit}
            profile={editableProfile}
          />
          : <ReadOnlyProfile profile={profile} />
        }
      </>
    );
  }
}