import React from 'react';
import EditableProject from './editableProject';
import ReadOnlyProject from './readOnlyProject';

export default class ProjectOverview extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const {
      project,
      editableProject,
      isEditable,
      onProjectFieldEdit
    } = this.props;

    return (
      <>
        {isEditable
          ? <EditableProject
            onProjectFieldEdit={onProjectFieldEdit}
            project={editableProject}
          />
          : <ReadOnlyProject project={project} />
        }
      </>
    );
  }
}