import gql from 'graphql-tag';
import { client } from './graphqlClient';

const GET_PROJ_COUNT_QUERY = gql`
  query {
    allProjects {
      totalCount
    }
  }
`;

const GET_PROJECT_QUERY = gql`
  query ($id: Int!) {
    projectById(id: $id) {
      description, directions
      name, languages
      selfJoinable, status, type
      id, interests, ownerId
      owner { id username }
      members {
        totalCount
        nodes { id username }
      }
      pendingMembers {
        nodes { id username }
        edges { id type status }
      }
    }
  }
`;

const GET_PROJECTS_QUERY = gql`
  query {
    allProjects(
      condition: { type: "public" }
    ) {
      edges {
        node {
          id, name, description
          languages, interests, status
          selfJoinable, type
          members { totalCount }
        }
      }
    }
  }
`;

const GET_FILTERED_PROJECTS_QUERY = gql`
  query ($search: String!) {
    allProjects(
      filter: {
        or: [
          { name: { includesInsensitive: $search } }
          { description: { includesInsensitive: $search } }
          { interestsText: { includesInsensitive: $search } }
          { languagesText: { includesInsensitive: $search } }
        ]
      },
      condition: { type: "public" }
    ) {
      edges {
        node {
          id, name, description
          languages, interests, status
          selfJoinable, type
          members { totalCount }
        }
      }
    }
  }
`;

const GET_OWNED_PROJECTS_QUERY = gql`
  query ($profileId: Int!) {
    profileById(id: $profileId) {
      ownedProjects {
        nodes { id name }
      }
    }
  }
`;

const CREATE_PROJECT_MUTATION = gql` 
  mutation (
    $ownerId: Int!
    $name: String!
    $description: String!
    $directions: String!
    $languages: [String]!
    $interests: [String]!
    $selfJoinable: Boolean!
    $status: String!
    $type: String!
  ) {
    createProject(
      input: {
        project: {
          ownerId: $ownerId
          name: $name
          description: $description
          directions: $directions
          languages: $languages
          interests: $interests
          selfJoinable: $selfJoinable
          status: $status
          type: $type
        }
      }
    ) {
      clientMutationId
      project {
        id
      }
    }
  }`;

const UPDATE_PROJ_MUTATION = gql`
  mutation(
    $id: Int!
    $name: String!
    $description: String!
    $directions: String!
    $languages: [String]!
    $interests: [String]!
    $selfJoinable: Boolean!
    $status: String!
    $type: String!
  ) {
    updateProjectById(
      input: {
        id: $id
        projectPatch: {
          name: $name
          description: $description
          directions: $directions
          languages: $languages
          interests: $interests
          selfJoinable: $selfJoinable
          status: $status
          type: $type
        }
      }
    ) {
      clientMutationId
    }
  }
`;

const DELETE_PROJECT_MUTATION = gql`
  mutation($id: Int!) {
    deleteProjectById(input: {id: $id}) {
      clientMutationId
    }
  }
`;

async function getProjCount() {
  try {
    const res = await client.query({
      query: GET_PROJ_COUNT_QUERY
    });
    return res.data.allProjects.totalCount;
  } catch (error) {
    throw error;
  }
}

async function updateProject(project) {
  try {
    await client.mutate({
      mutation: UPDATE_PROJ_MUTATION,
      variables: project
    });
  } catch (error) {
    throw error;
  }
}

async function deleteProject(id) {
  try {
    await client.mutate({
      mutation: DELETE_PROJECT_MUTATION,
      variables: { id }
    });
  } catch (error) {
    throw error;
  }
}

async function getProject(id) {
  try {
    const res = await client.query({
      query: GET_PROJECT_QUERY,
      variables: { id }
    });
    const project = res.data.projectById;

    // Including request type/status for each pending member
    project.pendingMembers.nodes =
      project.pendingMembers.nodes.map((p, i) => ({
        ...p,
        requestId: project.pendingMembers.edges[i].id,
        requestStatus: project.pendingMembers.edges[i].status,
        requestType: project.pendingMembers.edges[i].type
      }));
    
    return project;
  } catch (error) {
    throw error;
  }
}

async function getProjects() {
  try {
    const res = await client.query({
      query: GET_PROJECTS_QUERY
    });

    return res.data.allProjects.edges.map(p => p.node);
  } catch (error) {
    throw error;
  }
}

async function getFilteredProjects(search) {
  try {
    const res = await client.query({
      query: GET_FILTERED_PROJECTS_QUERY,
      variables: { search }
    });

    return res.data.allProjects.edges.map(p => p.node);
  } catch (error) {
    throw error;
  }
}

async function createProject(project) {
  try {
    const res = await client.mutate({
      mutation: CREATE_PROJECT_MUTATION,
      variables: project
    });
    return res.data.createProject.project.id;
  } catch (error) {
    throw error;
  }
}

async function getOwnedProjects(profileId) {
  try {
    const res = await client.query({
      query: GET_OWNED_PROJECTS_QUERY,
      variables: { profileId }
    });

    return res.data.profileById.ownedProjects.nodes;
  } catch (error) {
    throw error;
  }
}

export default {
  getProjects,
  getFilteredProjects,
  getOwnedProjects,
  getProject,
  createProject,
  deleteProject,
  updateProject,
  getProjCount
};
