import React from "react";
import { Button } from 'react-bootstrap';
import Modal from 'react-bootstrap/Modal';
import ImageController from '../../controllers/imageController';
import AvatarEditor from 'react-avatar-editor';
import Dropzone from 'react-dropzone';
import DEFAULT_PROFILE from '../../images/default-profile.png';
import '../../styles/profilePictureModal.scss';

export default class ProfilePictureModal extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      image: null,
      isLoading: false
    };

    this.setEditorRef = this.setEditorRef.bind(this);
    this.selectImage = this.selectImage.bind(this);
    this.upload = this.upload.bind(this);
  }

  setEditorRef(editor) {
    this.editor = editor;
  }

  async upload() {
    const { onConfirm } = this.props;
    this.setState({ isLoading: true });
    const editedImage = this.editor.getImage();

    editedImage.toBlob(async (blob) => {
      try {
        const uploadURL = await ImageController.upload(blob);
        onConfirm(uploadURL);
      } catch (error) {
        console.log(error);
        onConfirm(null);
      }

      this.setState({
        image: null,
        isLoading: false
      });
    });
  }

  selectImage(image) {
    this.setState({ image: image[0] });
  }

  render() {
    const { image, isLoading } = this.state;
    const { show, onClose } = this.props;
    const confirmDisabled = image === null || isLoading;

    return (
      <Modal
        className="profile-picture-modal"
        show={show}
        onHide={onClose}
        size="md"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title>
            Upload Profile Picture
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="info">
            Drag a new profile picture below:
          </div>
          <div className="dropzone">
            <Dropzone
              onDrop={this.selectImage}
              noKeyboard
              noClick
            >
              {({ getRootProps, getInputProps }) => (
                <div {...getRootProps()}>
                  <AvatarEditor
                    ref={this.setEditorRef}
                    image={image || DEFAULT_PROFILE}
                    border={4}
                  />
                  <input {...getInputProps()} />
                </div>
              )}
            </Dropzone>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="dark" onClick={onClose}>Close</Button>
          <Button
            variant="dark"
            onClick={() => this.upload(image)}
            disabled={confirmDisabled}
          >
            {isLoading ? 'Loading...' : 'Confirm'}
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}