import gql from 'graphql-tag';
import { client } from './graphqlClient';

const ACCEPT_MUTATION = gql`
  mutation ($profileId: Int!, $projectId: Int!) {
    updateRequestByProfileIdAndProjectId(input: {
      requestPatch: {
        status: "accepted"
      }, 
      profileId: $profileId
      projectId: $projectId
    }) { clientMutationId }
    
    createMembership(input: {
      membership: {
        profileId: $profileId
        projectId: $projectId
      }
    }) { clientMutationId }
  }
`;

const DECLINE_MUTATION = gql`
  mutation ($requestId: Int!) {
    updateRequestById(input: {
      requestPatch: {
        status: "declined"
      }, id: $requestId
    }) { clientMutationId }
  }
`;

const CREATE_MEMBERSHIP_MUTATION = gql`
  mutation($profileId: Int!, $projectId: Int!) {
    createMembership (
      input: {
        membership: {
          profileId: $profileId
          projectId: $projectId
        }
      }
    ) { clientMutationId }
  }
`;

const DELETE_MEMBERSHIP_MUTATION = gql`
  mutation ($profileId: Int!, $projectId: Int!) {
    deleteMembershipByProfileIdAndProjectId(
      input: {profileId: $profileId, projectId: $projectId}
    ) { clientMutationId }
  }
`;

const CREATE_INVITE_MUTATION = gql`
  mutation($profileId: Int!, $projectId: Int!) {
    createRequest (
      input: {
        request: {
          profileId: $profileId
          projectId: $projectId
          type: "invite"
        }
      }
    ) { clientMutationId }
  }
`;

const CREATE_REQUEST_MUTATION = gql`
  mutation($profileId: Int!, $projectId: Int!) {
    createRequest (
      input: {
        request: {
          profileId: $profileId
          projectId: $projectId
          type: "request"
        }
      }
    ) { clientMutationId }
  }
`;

async function accept(profileId, projectId) {
  try {
    await client.mutate({
      mutation: ACCEPT_MUTATION,
      variables: { profileId, projectId }
    });
  } catch (error) {
    throw error;
  }
}

async function decline(requestId) {
  try {
    await client.mutate({
      mutation: DECLINE_MUTATION,
      variables: { requestId }
    });
  } catch (error) {
    throw error;
  }
}

async function requestToJoinProject(profileId, projectId) {
  try {
    await client.mutate({
      mutation: CREATE_REQUEST_MUTATION,
      variables: { profileId, projectId }
    });
  } catch (error) {
    throw error;
  }
}

async function joinProject(profileId, projectId) {
  try {
    await client.mutate({
      mutation: CREATE_MEMBERSHIP_MUTATION,
      variables: { profileId, projectId }
    });
  } catch (error) {
    throw error;
  }
}

async function leaveProject(profileId, projectId) {
  try {
    await client.mutate({
      mutation: DELETE_MEMBERSHIP_MUTATION,
      variables: { profileId, projectId }
    });
  } catch (error) {
    throw error;
  }
}

async function inviteToProject(profileId, projectId) {
  try {
    await client.mutate({
      mutation: CREATE_INVITE_MUTATION,
      variables: { profileId, projectId }
    });
  } catch (error) {
    throw error;
  }
}

export default {
  accept,
  decline,
  requestToJoinProject,
  joinProject,
  leaveProject,
  inviteToProject
};