import React from 'react';
import Card from 'react-bootstrap/Card';
import Nav from 'react-bootstrap/Nav';
import Button from 'react-bootstrap/Button';
import JoinProjectButton from '../../components/project/joinProjectButton';
import "../../styles/project.scss";
import { FaEdit, FaTrashAlt, FaCheck, FaTimes } from 'react-icons/fa';
import UserController from '../../controllers/userController';
import ConfirmationWrapper from '../confirmationWrapper';
import { ProjectTab } from '../../pages/project';

const DELETE_CONFIRMATION_MESSAGE = 'Are you sure you want to delete this project?';
const LEAVE_CONFIRMATION_MESSAGE = 'Are you sure you want to leave the project?';

export default class ProjectHeader extends React.Component {
  constructor(props) {
    super(props);
  }

  renderNav(project) {
    const { onTabChange, tab } = this.props;

    const isOwnProject = UserController.isLoggedIn()
      && UserController.getToken().sub === project.ownerId;

    if (!isOwnProject) {
      return null;
    }

    return (
      <>
        <Nav fill variant="pills" activeKey={tab}>
          <Nav.Item className="overview-tab" onClick={() => onTabChange(ProjectTab.OVERVIEW)}>
            <Nav.Link eventKey="overview">Overview</Nav.Link>
          </Nav.Item>
          <Nav.Item className="manage-members" onClick={() => onTabChange(ProjectTab.MANAGE)}>
            <Nav.Link eventKey="manage">Manage Members</Nav.Link>
          </Nav.Item>
        </Nav>
        <hr />
      </>
    );
  }

  render() {
    const {
      tab,
      project,
      isEditable,
      isEditValid,
      onJoin,
      onLeave,
      onInvite,
      onToggleEditable,
      onSave,
      onDelete
    } = this.props;

    const isOwnProject = UserController.isLoggedIn()
      && UserController.getToken().sub === project.owner.id;

    const isMember = UserController.isLoggedIn()
      && !isOwnProject
      && project.members.nodes.some(
        m => m.id === UserController.getToken().sub
      );

    return (
      <Card.Header className="project-header">
        {this.renderNav(project)}
        <h2><b>
          {project.name}
          {isOwnProject && tab === ProjectTab.OVERVIEW && (
            <>
              {isEditable &&
                <>
                  {isEditValid && <FaCheck title="Save Changes" onClick={onSave} />}
                  <FaTimes title="Revert Changes" onClick={onToggleEditable} />
                </>
              }
              {!isEditable &&
                <>
                  <FaEdit className="edit-toggle" title="Edit Project" onClick={onToggleEditable} />
                  <ConfirmationWrapper
                    message={DELETE_CONFIRMATION_MESSAGE}
                    onConfirm={onDelete}
                    >
                    <FaTrashAlt className="trash-toggle" title="Delete Project" />
                  </ConfirmationWrapper>
                </>
              }
            </>
          )}
        </b></h2>

        {isOwnProject && tab === ProjectTab.MANAGE && (
          <Button onClick={onInvite} size="sm" variant="dark">Invite Member</Button>
        )}

        <JoinProjectButton project={project} onClick={onJoin} />
        {isMember &&
          <>
            <ConfirmationWrapper
              message={LEAVE_CONFIRMATION_MESSAGE}
              onConfirm={onLeave}
            >
              <Button variant="dark"
                size="sm"
                className="leave-project-button">
                Leave Project
              </Button>
            </ConfirmationWrapper>
          </>
        }
      </Card.Header>
    );
  }
}