import React, { Component } from 'react';
import { Helmet } from 'react-helmet';
import Layout from "../components/layout";
import { Container, Row, Col, FormControl } from "react-bootstrap";
import ProfileController from '../controllers/profileController';
import InfiniteScroll from "react-infinite-scroll-component";
import ScrollToTop from 'react-scroll-up';
import "../styles/profile.scss";
import ProfileCard from "../components/profile/profileCard";
import Loading from '../components/loading';
import '../styles/people.scss';
import { FaArrowUp } from "react-icons/fa";
import { debounce } from 'throttle-debounce';

const DEBOUNCE_MS = 200;
const PROFILES_PER_FETCH = 9;
const SCROLL_TO_TOP_SHOW_UNDER = 260;

export default class People extends Component {
  constructor(props) {
    super(props);
    this.state = {
      profiles: [],
      displayedProfiles: [],
      search: '',
      finishedLoading: false,
    };

    this.retrieveProfiles = debounce(
      DEBOUNCE_MS,
      this.retrieveProfiles.bind(this)
    );
    this.updateSearch = this.updateSearch.bind(this);
    this.fetchMoreProfiles = this.fetchMoreProfiles.bind(this);
    this.renderHeader = this.renderHeader.bind(this);
    this.renderProfiles = this.renderProfiles.bind(this);
  }

  async componentDidMount() {
    this.retrieveProfiles();
  }

  async retrieveProfiles(search) {
    this.setState({ finishedLoading: false });

    try {
      const profiles = search
        ? await ProfileController.getFilteredProfiles(search)
        : await ProfileController.getProfiles();

      this.setState({
        profiles,
        displayedProfiles: profiles.slice(0, PROFILES_PER_FETCH)
      });
    } catch (error) {
      console.log(error);
    }

    this.setState({ finishedLoading: true });
  }

  updateSearch(search) {
    this.setState({ search });
  }

  fetchMoreProfiles() {
    const { profiles, displayedProfiles } = this.state;
    const fetchedProfiles = profiles.slice(
      displayedProfiles.length,
      displayedProfiles.length + PROFILES_PER_FETCH
    );

    this.setState({
      displayedProfiles: displayedProfiles.concat(fetchedProfiles)
    });
  }

  renderHeader() {
    const { search } = this.state;

    return (
      <>
        <h1><span>👩‍🔬</span> People</h1>
        <p>
          Find team members for your next project!
        </p>
        <FormControl
          type="text"
          size="lg"
          className="search"
          placeholder="Search by username, skills, etc..."
          value={search}
          onChange={async (event) => {
            const search = event.target.value;
            this.updateSearch(search);
            await this.retrieveProfiles(search);
          }}
        />
      </>
    );
  }

  renderProfiles() {
    const { profiles, displayedProfiles, finishedLoading} = this.state;
    const hasMore = profiles.length > displayedProfiles.length;

    if (!finishedLoading) {
      return <Loading />
    }

    if (profiles.length === 0) {
      return <h4>No profiles match the search.</h4>;
    }

    return (
      <InfiniteScroll
        dataLength={displayedProfiles.length}
        next={this.fetchMoreProfiles}
        hasMore={hasMore}
        loader={<span className="infinite-load-text">Loading...</span>}
      >
        <Row>
          {displayedProfiles.map(p => {
            return (
              <Col md="4" key={p.id}>
                <ProfileCard profile={p} />
              </Col>
            )
          })}
        </Row>
    </InfiniteScroll>
    );
  }

  render() {
    return (
      <>
        <Helmet>
          <title>People</title>
        </Helmet>
        <Layout className="people-page">
          <Container fluid>
            {this.renderHeader()}
            {this.renderProfiles()}
          </Container>
          <ScrollToTop showUnder={SCROLL_TO_TOP_SHOW_UNDER}>
            <h2><FaArrowUp title="Scroll to top" /></h2>
          </ScrollToTop>
        </Layout>
      </>
    );
  }
}