import React from 'react';
import { Helmet } from 'react-helmet';
import { withRouter } from 'react-router-dom';
import Layout from '../../components/layout';
import { Card, Button, Row, Col } from 'react-bootstrap';
import EditableProject from '../../components/project/editableProject';
import UserController from '../../controllers/userController';
import ProjectController from '../../controllers/projectController';
import WebController from '../../controllers/webController';
import '../../styles/project.scss';
import '../../styles/createProject.scss';

class CreateProject extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      project: {
        ownerId: null,
        name: '',
        description: '',
        directions: '',
        languages: [],
        interests: [],
        selfJoinable: true,
        status: 'Planning Phase',
        type: 'public'
      }
    }

    this.onProjectFieldEdit = this.onProjectFieldEdit.bind(this);
    this.createProject = this.createProject.bind(this);
  }

  async componentDidMount() {
    if (!UserController.isLoggedIn()) {
      WebController.navigate('/');
    } else {
      const id = UserController.getToken().sub;
      const { project } = this.state;
      project.ownerId = id;
      this.setState({ project });
    }
  }

  onProjectFieldEdit(field, value) {
    const { project } = this.state;
    project[field] = value;
    this.setState({ project });
  }

  async createProject() {
    const { project } = this.state;
    try {
      const projectId = await ProjectController.createProject(project);
      WebController.navigate(`/#/project/${projectId}`);
    } catch (error) {
      console.log(error);
    }
  }

  render() {
    const { project } = this.state;
    const isCreateDisabled = project.name.trim() === '';

    return (
      <Layout className={"project-page create-project-page"}>
        <Helmet>
          <title>Create a Project</title>
        </Helmet>
        <Row>
          <Col>
            <Card className="project-container" bg="secondary" border="secondary">
              <Card.Header>
                <br />
                <h2><span>💡</span> Create a Project</h2>
              </Card.Header>
              <Card.Body>
                <EditableProject
                  project={project}
                  onProjectFieldEdit={this.onProjectFieldEdit}
                />
                <hr />
                <Button
                  variant="dark"
                  size="lg"
                  onClick={this.createProject}
                  block
                  disabled={isCreateDisabled}
                >
                  Create
                </Button>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Layout>
    );
  }
}

export default withRouter(CreateProject);