
import React from 'react';
import RequestController from '../../controllers/requestController';
import ListGroup from 'react-bootstrap/ListGroup';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import Button from 'react-bootstrap/Button';
import ConfirmationWrapper from '../confirmationWrapper';

const KICK_CONFIRMATION_MESSAGE = 'Are you sure you want to kick this user from the project?';

export default class ManageProjectMembers extends React.Component {
  constructor(props) {
    super(props);

    this.accept = this.accept.bind(this);
    this.decline = this.decline.bind(this);
  }

  async accept(profileId, projectId) {
    try {
      await RequestController.accept(profileId, projectId);
      window.location.reload();
    } catch (error) {
      console.log(error);
    }
  }

  async decline(requestId) {
    try {
      await RequestController.decline(requestId);
      window.location.reload();
    } catch (error) {
      console.log(error);
    }
  }

  async kick(profileId, projectId) {
    try {
      await RequestController.leaveProject(profileId, projectId);
      window.location.reload();
    } catch (error) {
      console.log(error);
    }
  }

  renderMembers() {
    const { project } = this.props;
    const members = project.members.nodes;

    if (members.length === 0) {
      return (
        <>
          <h4>Members</h4>
          <span className="message">
            The project does not have any members.
          </span>
          <br />
        </>
      );
    }

    return (
      <>
        <h4>Members</h4>
        <ListGroup>
          {members.map(p => (
            <ListGroup.Item key={p.requestId} className="request-list-item">
              <a href={`/#/profile/${p.username}`}>{p.username}</a>
              <ButtonGroup className="request-actions">
                <ConfirmationWrapper
                  message={KICK_CONFIRMATION_MESSAGE}
                  onConfirm={() => this.kick(p.id, project.id)}
                >
                  <Button variant="danger" size="sm">
                    Kick
                  </Button>
                </ConfirmationWrapper>
              </ButtonGroup>
            </ListGroup.Item>
          ))}
        </ListGroup>
      </>
    );
  }

  renderPendingMembers() {
    const { project } = this.props;

    if (project.selfJoinable) {
      return null;
    }

    const pendingMembers = project.pendingMembers.nodes.filter(
      p => p.requestStatus === 'pending' && p.requestType === 'request'
    );

    if (pendingMembers.length === 0) {
      return (
        <>
          <hr />
          <h4>Pending Members</h4>
          <span className="message">
            Nobody is requesting to join the project.
          </span>
        </>
      );
    }

    return (
      <>
        <hr />
        <h4>Pending Members</h4>
        <ListGroup>
          {pendingMembers.map(p => (
            <ListGroup.Item key={p.requestId} className="request-list-item">
              <a href={`/#/profile/${p.username}`}>{p.username}</a>
              <ButtonGroup className="request-actions" size="sm">
                <Button
                  variant="success"
                  onClick={() => this.accept(p.id, project.id)}
                >
                  Accept
                </Button>
                <Button
                  variant="danger"
                  onClick={() => this.decline(p.requestId)}
                >
                  Decline
                </Button>
              </ButtonGroup>
            </ListGroup.Item>
          ))}
        </ListGroup>
      </>
    );
  }

  render() {
    return (
      <>
        {this.renderMembers()}
        {this.renderPendingMembers()}
      </>
    );
  }
}