function hasShownProfileNux() {
  return parseBool(localStorage.getItem('hasShownProfileNux'));
}

function hasShownOwnerProjectNux() {
  return parseBool(localStorage.getItem('hasShownOwnerProjectNux'));
}

function hasShownMemberProjectNux() {
  return parseBool(localStorage.getItem('hasShownMemberProjectNux'));
}

function setProfileNuxShown() {
  localStorage.setItem('hasShownProfileNux', 'true');
}

function setOwnerProjectNuxShown() {
  localStorage.setItem('hasShownOwnerProjectNux', 'true');
}

function setMemberProjectNuxShown() {
  localStorage.setItem('hasShownMemberProjectNux', 'true');
}

function parseBool(bool) {
  return bool === 'true' || false;
}

export default {
  hasShownProfileNux,
  hasShownOwnerProjectNux,
  hasShownMemberProjectNux,
  setProfileNuxShown,
  setOwnerProjectNuxShown,
  setMemberProjectNuxShown
};