import React from 'react';
import Card from 'react-bootstrap/Card';
import Badge from 'react-bootstrap/Badge';

export const DEFAULT_BIO =
  'Apparently, this user prefers to keep an air of mystery about them.';

export default function ReadOnlyProfile({ profile }) {
  return (
    <>
      <h4><span>👩‍🔬</span> Bio</h4>
      <Card.Text>
        {profile.bio || DEFAULT_BIO}
      </Card.Text>

      <hr />
      <h4><span>🧪</span> Interests</h4>
      <div className="profile-badges">
        {profile.interests.map(interest => (
          <Badge pill key={interest} variant="dark">
            {interest}
          </Badge>
        ))}
      </div>
      <hr />
      <h4><span>🧰</span> Skills</h4>
      <div className="profile-badges">
        {profile.languages.map(language => (
          <Badge pill key={language} variant="dark">
            {language}
          </Badge>
        ))}
      </div>
    </>
  );
}