import React from 'react';
import { Helmet } from 'react-helmet';
import { Container, Col, Row } from 'react-bootstrap';
import Layout from '../components/layout';
import Register from '../components/register';
import HomeTimeline from '../components/homeTimeline';
import ProjectController from '../controllers/projectController';
import ProfileController from '../controllers/profileController';
import UserController from '../controllers/userController';
import '../styles/home.scss';

export default class Home extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      projectCount: 0,
      profileCount: 0
    };
  }

  async componentDidMount() {
    UserController.handleGitHubLogin();

    try {
      const projectCount = await ProjectController.getProjCount();
      const profileCount = await ProfileController.getProfCount();
      this.setState({ projectCount, profileCount });
    } catch (error) {
      console.log(error);
    }
  }

  render() {
    const { profileCount, projectCount } = this.state;

    return (
      <>
        <Layout className="home-page">
          <Helmet>
            <title>Quaranteam</title>
          </Helmet>
          <Container >
            <Row>
              <Col xs={{ span: 12 }} md={{ span: 6 }} className="home-text">
                <h1 className="home-header">
                  <span>🧬</span> Quaranteam
              </h1>
                <br />
                <p>
                  Perhaps your internship was cancelled. Or perhaps you're simply bored.
                  Creating a side project is a fun and effective way to continue improving
                  your development skills.
              </p>
                <p>
                  <b>Quaranteam</b> is the best platform for finding projects and team members.
                Register to join <b>{profileCount}</b> members working
                on <b>{projectCount}</b> projects!
              </p>
              </Col>
              <Col xs={{ span: 12 }} md={{ span: 6 }}>
                <Register />
              </Col>
            </Row>
            <br />
            <HomeTimeline />
          </Container>
          <footer>
            <p>Quaranteam.tech © 2020</p>
          </footer>
        </Layout>
        <div className="stars"></div>
        <div className="twinkling"></div>
      </>
    );
  }
}